import React from 'react';
import './index.css';

const Greeting = (props) => {
  return (
    <div id="greeting">
      <h1>Hi,</h1>
      <h2>I'm Alex</h2>
      <h3>A Web Developer from Austin, TX</h3>
    </div>
  );
};

export default Greeting;



